import React from 'react'
import Navbar from '../Components/Navbar'
import SubNavbar from '../Components/SubNavbar'
import PageDetails from './PageDetails'
import SinglerProperty from './SinglerProperty'
import BestProSection from './BestProSection'
import FooterDeyails from './FooterDeyails'

function AllDetails() {
  return (
    <div>
        <SubNavbar/>
        <Navbar/>
        <PageDetails/>
        <SinglerProperty/>
        <BestProSection/>
        <FooterDeyails/>
    </div>
  )
}

export default AllDetails